<template>
  <div class="container" id="indexPage">
    <div class="header-box flex-center">
      <div class="side-left flex-center">
        <img src="~@/assets/images/back@2x.png" alt="">
      </div>
      <van-tabs v-model:active="tabsActive" @click="tabClick" type="card" class="flex-item middle">
        <!-- 期权 -->
        <van-tab title="期权" name="1"></van-tab>
        <!-- 矿池 -->
        <van-tab title="矿池" name="2"></van-tab>
      </van-tabs>
      <div class="side-right flex-center" @click="$router.push({name: 'Share'})">
        <img src="~@/assets/images/share.png" alt="">
      </div>
    </div>
    <div class="top-tip flex-middle">
      <van-swipe style="height: 30px;" :autoplay="3000" vertical :show-indicators="false">
        <van-swipe-item class="row">
          15:00恭喜0xsd...dddd获得150.00wind token和123412341234
        </van-swipe-item>
        <van-swipe-item class="row">
          16:00恭喜0xsd...dddd获得160.00wind token和1234123421354
        </van-swipe-item>
        <van-swipe-item class="row">
          17:00恭喜0xsd...dddd获得170.00wind token和634563463456
        </van-swipe-item>
        <van-swipe-item class="row">
          18:00恭喜0xsd...dddd获得180.00wind token和45234523452345
        </van-swipe-item>
      </van-swipe>
    </div>
    <!-- 期权 -->
    <template v-if="tabsActive === '1'">
      <index-option></index-option>
    </template>
    <!-- 矿池 -->
    <template v-if="tabsActive === '2'">
      <index-pool></index-pool>
    </template>
  </div>
</template>
<script>
import { reactive, toRefs } from "vue";
import { useRoute, useRouter } from 'vue-router'
import IndexOption from "./IndexOption";
import IndexPool from "./IndexPool";
export default {
  components: {
    IndexOption,
    IndexPool
  },
  setup() {
    const route = useRoute()
    const router = useRouter()
    const data = reactive({
      tabsActive: route.query.name,
      tabClick: (name) => {
        // console.log(name)
        router.replace({
          name: 'Index',
          query: {
            name: name
          }
        })
      },
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
};
</script>
